<style>
  
</style>

<template>
  <div class="add-form-wraper">
    <el-form ref="form" :model="addParamMesg" label-width="100px">
      <el-form-item label="分类名称" required>
        <el-input v-model="addParamMesg.categoryName" size="small" class="add-input" placeholder="请输入分类名称"></el-input>
        <span>限制8个汉字(16个字符)</span>
      </el-form-item>

      <el-form-item label="备注">
        <el-input v-model="addParamMesg.remark" size="small" class="add-input" placeholder="请输入备注"></el-input>
      </el-form-item>

      <el-form-item label="排序">
        <el-input v-model="addParamMesg.level" size="small" class="add-input" placeholder="请输入排序"></el-input>
        <span>0-99的整数，数值越大越靠前</span>
      </el-form-item>

      <hr class="s-slip-tb">

      <el-form-item>
        <el-button type="primary" size="mini" @click="doAddHandel">保&nbsp;存</el-button>
        <el-button size="mini" @click="doCancelHandel">取&nbsp;消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import * as utils from '@/utils/utils'
const categoryMgtUrl = {
  updateCategory: '/plateform/a/category/updateCategory',
  getCategoryList: '/plateform/a/category/getCategoryList'
}

export default {
  data() {
    return {
      addParamMesg: {
        categoryName: '', // 分类名称
        remark: '', // 备注
        level: 0 // 排序
      },
      rowMesg: {},
      categoryId: '' // 分类id
    }
  },
  created() {
    this.categoryId = this.$route.params.id
    this.getDetailMesg()
  },
  methods: {
    /**
     * [getDetailMesg 获取详情]
     * @return {[type]} [description]
     */
    getDetailMesg() {
      // 全局展示loading
      utils.showLoading()
      this.$http.post(categoryMgtUrl.getCategoryList, { categoryId: this.categoryId }).then(res => {
        // 清除全局loading
        utils.clearLoading()
        if(res.code === 0) {
          if(res.data && res.data.length) {
            this.addParamMesg = res.data[0]
          }else {
            let defaultParam = {
              categoryName: '', // 分类名称
              remark: '', // 备注
              level: 0 // 排序
            }
            this.addParamMesg = defaultParam
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 清除全局loading
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [doAddHandel 添加]
     * @return {[type]} [description]
     */
    doAddHandel() {
      let categoryNameReg = /^[\u4E00-\u9FA5]{1,8}$|^[A-Za-z]{1,16}$/
      let sortNumReg = /^[1-9]*[1-9][0-9]*$/
      if(this.addParamMesg.categoryName == "") {
        this.$message("请输入分类名称")
        return
      }else {
        if(this.addParamMesg.categoryName.length > 16) {
          this.$message("分类名称限制长度不超过16")
          return
        }
      } 

      if(this.addParamMesg.level == "" || this.addParamMesg.level == 0) {
        this.addParamMesg.level = 0
      }else {
        if(sortNumReg.test(this.addParamMesg.level)) {
          if(this.addParamMesg.level < 0 || this.addParamMesg.level > 99) {
            this.$message("排序为0-99的整数")
            return
          }
        }else {
          this.$message("排序为0-99的整数")
          return
        }
      } 
      // 全局展示loading
      utils.showLoading()
      this.$http.post(categoryMgtUrl.updateCategory, { ...this.addParamMesg }).then(res => {
        // 清除全局loading
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("修改分类成功")
          this.$router.push('/categories_mgt')
        }
      }).catch(e => {
        // 清除全局loading
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [doCancelHandel 取消添加]
     * @return {[type]} [description]
     */
    doCancelHandel() {
      this.$router.go(-1)
    }
  }

}
</script>



